<h1 id="卡券" tabindex="-1"><a class="header-anchor" href="#卡券" aria-hidden="true">#</a> 卡券</h1>
<ul>
<li></li>
</ul>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">card</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="通用功能" tabindex="-1"><a class="header-anchor" href="#通用功能" aria-hidden="true">#</a> 通用功能</h2>
<h3 id="获取卡券颜色" tabindex="-1"><a class="header-anchor" href="#获取卡券颜色" aria-hidden="true">#</a> 获取卡券颜色</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">colors</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="卡券开放类目查询" tabindex="-1"><a class="header-anchor" href="#卡券开放类目查询" aria-hidden="true">#</a> 卡券开放类目查询</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">categories</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="创建卡券" tabindex="-1"><a class="header-anchor" href="#创建卡券" aria-hidden="true">#</a> 创建卡券</h3>
<p>创建卡券接口是微信卡券的基础接口，用于创建一类新的卡券，获取card_id，创建成功并通过审核后，商家可以通过文档提供的其他接口将卡券下发给用户，每次成功领取，库存数量相应扣除。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$cardType</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'member_card'</span><span class="token punctuation">,</span> <span class="token keyword type-hint">array</span> <span class="token variable">$attributes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>attributes</code> array 卡券信息</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>

	<span class="token variable">$cardType</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'GROUPON'</span><span class="token punctuation">;</span>

    <span class="token variable">$attributes</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'base_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token string single-quoted-string">'brand_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'微信餐厅'</span><span class="token punctuation">,</span>
          <span class="token string single-quoted-string">'code_type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'CODE_TYPE_TEXT'</span><span class="token punctuation">,</span>
          <span class="token string single-quoted-string">'title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'132元双人火锅套餐'</span><span class="token punctuation">,</span>
          <span class="token comment">//...</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
      <span class="token string single-quoted-string">'advanced_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token string single-quoted-string">'use_condition'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
              <span class="token string single-quoted-string">'accept_category'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'鞋类'</span><span class="token punctuation">,</span>
              <span class="token string single-quoted-string">'reject_category'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'阿迪达斯'</span><span class="token punctuation">,</span>
              <span class="token string single-quoted-string">'can_use_with_other_discount'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
          <span class="token punctuation">]</span><span class="token punctuation">,</span>
          <span class="token comment">//...</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$cardType</span><span class="token punctuation">,</span> <span class="token variable">$attributes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br></div></div><h3 id="获取卡券详情" tabindex="-1"><a class="header-anchor" href="#获取卡券详情" aria-hidden="true">#</a> 获取卡券详情</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardInfo</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="批量查询卡列表" tabindex="-1"><a class="header-anchor" href="#批量查询卡列表" aria-hidden="true">#</a> 批量查询卡列表</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token variable">$offset</span> <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token variable">$count</span> <span class="token operator">=</span> <span class="token number">10</span><span class="token punctuation">,</span> <span class="token variable">$statusList</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'CARD_STATUS_VERIFY_OK'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>offset</code> int - 查询卡列表的起始偏移量，从0开始</li>
<li><code>count</code> int - 需要查询的卡片的数量</li>
<li><code>statusList</code> -  支持开发者拉出指定状态的卡券列表，详见 example</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// CARD_STATUS_NOT_VERIFY, 待审核；</span>
<span class="token comment">// CARD_STATUS_VERIFY_FAIL, 审核失败；</span>
<span class="token comment">// CARD_STATUS_VERIFY_OK， 通过审核；</span>
<span class="token comment">// CARD_STATUS_USER_DELETE，卡券被商户删除；</span>
<span class="token comment">// CARD_STATUS_DISPATCH，在公众平台投放过的卡券；</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token variable">$offset</span><span class="token punctuation">,</span> <span class="token variable">$count</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'CARD_STATUS_NOT_VERIFY'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h3 id="更改卡券信息接口" tabindex="-1"><a class="header-anchor" href="#更改卡券信息接口" aria-hidden="true">#</a> 更改卡券信息接口</h3>
<p>支持更新所有卡券类型的部分通用字段及特殊卡券中特定字段的信息。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$attributes</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>type</code> string - 卡券类型</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uCzKWebwgNjxosee0ZuO3Os'</span><span class="token punctuation">;</span>

<span class="token variable">$type</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'groupon'</span><span class="token punctuation">;</span>

<span class="token variable">$attributes</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
  <span class="token string single-quoted-string">'base_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'logo_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/2aJY6aCPatSeibYAyy7yct9zJXL9WsNVL4JdkTbBr184gNWS6nibcA75Hia9CqxicsqjYiaw2xuxYZiaibkmORS2oovdg/0'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'顶部居中按钮'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'按钮下方的wording'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.easywechat.com'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'立即使用'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'6个汉字tips'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'promotion_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'更多优惠'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'promotion_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">,</span>
  <span class="token comment">//...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$attributes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br></div></div><h3 id="删除卡券" tabindex="-1"><a class="header-anchor" href="#删除卡券" aria-hidden="true">#</a> 删除卡券</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="创建二维码" tabindex="-1"><a class="header-anchor" href="#创建二维码" aria-hidden="true">#</a> 创建二维码</h3>
<p>开发者可调用该接口生成一张卡券二维码供用户扫码后添加卡券到卡包。</p>
<p>自定义 Code 码的卡券调用接口时，POST 数据中需指定 code，非自定义 code 不需指定，指定 openid 同理。指定后的二维码只能被用户扫描领取一次。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createQrCode</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>cards</code> array - 卡券相关信息</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// 领取单张卡券</span>
<span class="token variable">$cards</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'action_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'QR_CARD'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'expire_seconds'</span> <span class="token operator">=></span> <span class="token number">1800</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'action_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'card'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uFS2WWCFfbbEfsAzrzizVyY'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'is_unique_code'</span> <span class="token operator">=></span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'outer_id'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createQrCode</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// 领取多张卡券</span>
<span class="token variable">$cards</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'action_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'QR_MULTIPLE_CARD'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'action_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'multiple_card'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'card_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uFS2WWCFfbbEfsAzrzizVyY'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createQrCode</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>请求成功返回值示例：</p>
<div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
 <span class="token property">"errcode"</span><span class="token operator">:</span> <span class="token number">0</span><span class="token punctuation">,</span>
 <span class="token property">"errmsg"</span><span class="token operator">:</span> <span class="token string">"ok"</span><span class="token punctuation">,</span>
 <span class="token property">"ticket"</span><span class="token operator">:</span> <span class="token string">"gQHB8DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL0JIV3lhX3psZmlvSDZmWGVMMTZvAAIEsNnKVQMEIAMAAA=="</span><span class="token punctuation">,</span><span class="token comment">//获取ticket后需调用换取二维码接口获取二维码图片，详情见字段说明。</span>
 <span class="token property">"expire_seconds"</span><span class="token operator">:</span> <span class="token number">1800</span><span class="token punctuation">,</span>
 <span class="token property">"url"</span><span class="token operator">:</span> <span class="token string">"http://weixin.qq.com/q/BHWya_zlfioH6fXeL16o "</span><span class="token punctuation">,</span>
 <span class="token property">"show_qrcode_url"</span><span class="token operator">:</span> <span class="token string">"https://mp.weixin.qq.com/cgi-bin/showqrcode?  ticket=gQH98DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL0czVzRlSWpsamlyM2plWTNKVktvAAIE6SfgVQMEgDPhAQ%3D%3D"</span>
 <span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h3 id="ticket-换取二维码图片" tabindex="-1"><a class="header-anchor" href="#ticket-换取二维码图片" aria-hidden="true">#</a> ticket 换取二维码图片</h3>
<p>获取二维码 ticket 后，开发者可用 ticket 换取二维码图片。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQrCode</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>ticket</code> string&gt;  - 获取的二维码 ticket，凭借此 ticket 可以在有效时间内换取二维码。</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$ticket</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'gQFF8DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL01VTzN0T0hsS1BwUlBBYUszbVN5AAIEughxVwMEAKd2AA=='</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQrCode</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="ticket-换取二维码链接" tabindex="-1"><a class="header-anchor" href="#ticket-换取二维码链接" aria-hidden="true">#</a> ticket 换取二维码链接</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQrCodeUrl</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$ticket</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'gQFF8DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL01VTzN0T0hsS1BwUlBBYUszbVN5AAIEughxVwMEAKd2AA=='</span><span class="token punctuation">;</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQrCodeUrl</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="创建货架接口" tabindex="-1"><a class="header-anchor" href="#创建货架接口" aria-hidden="true">#</a> 创建货架接口</h3>
<p>开发者需调用该接口创建货架链接，用于卡券投放。创建货架时需填写投放路径的场景字段。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createLandingPage</span><span class="token punctuation">(</span><span class="token variable">$banner</span><span class="token punctuation">,</span> <span class="token variable">$pageTitle</span><span class="token punctuation">,</span> <span class="token variable">$canShare</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>banner</code> string -页面的 banner 图;</li>
<li><code>pageTitle</code> string - 页面的 title</li>
<li><code>canShare</code> bool - 页面是不是可以分享，true 或 false</li>
<li><code>scene</code>  string - 投放页面的场景值，具体值请参考下面的 example</li>
<li><code>cards</code>  array - 卡券列表，每个元素有两个字段</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$banner</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/iaL1LJM1mF9aRKPZJkmG8xXhiaHqkKSVMMWeN3hLut7X7hicFN'</span><span class="token punctuation">;</span>
<span class="token variable">$pageTitle</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'惠城优惠大派送'</span><span class="token punctuation">;</span>
<span class="token variable">$canShare</span>  <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">;</span>

<span class="token comment">//SCENE_NEAR_BY          附近</span>
<span class="token comment">//SCENE_MENU             自定义菜单</span>
<span class="token comment">//SCENE_QRCODE             二维码</span>
<span class="token comment">//SCENE_ARTICLE             公众号文章</span>
<span class="token comment">//SCENE_H5                 h5页面</span>
<span class="token comment">//SCENE_IVR                 自动回复</span>
<span class="token comment">//SCENE_CARD_CUSTOM_CELL 卡券自定义cell</span>
<span class="token variable">$scene</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'SCENE_NEAR_BY'</span><span class="token punctuation">;</span>

<span class="token variable">$cardList</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'thumb_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://test.digilinx.cn/wxApi/Uploads/test.png'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uJ37aU-tyRj4_grs8S45k1c'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'thumb_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://test.digilinx.cn/wxApi/Uploads/aa.jpg'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createLandingPage</span><span class="token punctuation">(</span><span class="token variable">$banner</span><span class="token punctuation">,</span> <span class="token variable">$pageTitle</span><span class="token punctuation">,</span> <span class="token variable">$canShare</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$cardList</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h3 id="图文消息群发卡券" tabindex="-1"><a class="header-anchor" href="#图文消息群发卡券" aria-hidden="true">#</a> 图文消息群发卡券</h3>
<blockquote>
<p>{warning} 特别注意：目前该接口仅支持填入非自定义 code 的卡券,自定义 code 的卡券需先进行 code 导入后调用。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getHtml</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getHtml</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="设置测试白名单" tabindex="-1"><a class="header-anchor" href="#设置测试白名单" aria-hidden="true">#</a> 设置测试白名单</h3>
<p>同时支持“openid”、“username”两种字段设置白名单，总数上限为10个。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelist</span><span class="token punctuation">(</span><span class="token variable">$openids</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 使用 openid</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelistByName</span><span class="token punctuation">(</span><span class="token variable">$usernames</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 使用 username</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><blockquote>
<ul>
<li><code>openids</code> array - 测试的openid列表</li>
<li><code>usernames</code> array&gt;  - 测试的微信号列表</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// by openid</span>
<span class="token variable">$openids</span>   <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token variable">$openid3</span><span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelist</span><span class="token punctuation">(</span><span class="token variable">$openids</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// by username</span>
<span class="token variable">$usernames</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'tianye0327'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'iovertrue'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelistByName</span><span class="token punctuation">(</span><span class="token variable">$usernames</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h3 id="获取用户已领取卡券接口" tabindex="-1"><a class="header-anchor" href="#获取用户已领取卡券接口" aria-hidden="true">#</a> 获取用户已领取卡券接口</h3>
<p>用于获取用户卡包里的，属于该appid下所有<strong>可用卡券，包括正常状态和未生效状态</strong>。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getUserCards</span><span class="token punctuation">(</span><span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$openid</span>  <span class="token operator">=</span> <span class="token string single-quoted-string">'odkJ9uDUz26RY-7DN1mxkznfo9xU'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span> <span class="token comment">// 卡券ID。不填写时默认查询当前 appid 下的卡券。</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getUserCards</span><span class="token punctuation">(</span><span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="设置微信买单接口" tabindex="-1"><a class="header-anchor" href="#设置微信买单接口" aria-hidden="true">#</a> 设置微信买单接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setPayCell</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$isOpen</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>isOpen</code> string - 是否开启买单功能，填 true/false，不填默认 true</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uH7u11R-Tu1kilbaW_zDFow'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setPayCell</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// isOpen = true</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setPayCell</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$isOpen</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="修改库存接口" tabindex="-1"><a class="header-anchor" href="#修改库存接口" aria-hidden="true">#</a> 修改库存接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">increaseStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$amount</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 增加库存</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">reductStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$amount</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 减少库存</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><blockquote>
<ul>
<li><code>cardId</code> string - 卡券 ID</li>
<li><code>amount</code> int - 修改多少库存</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">increaseStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token number">100</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="卡券-code" tabindex="-1"><a class="header-anchor" href="#卡券-code" aria-hidden="true">#</a> 卡券 Code</h2>
<h3 id="导入code接口" tabindex="-1"><a class="header-anchor" href="#导入code接口" aria-hidden="true">#</a> 导入code接口</h3>
<p>在自定义code卡券成功创建并且通过审核后，必须将自定义code按照与发券方的约定数量调用导入code接口导入微信后台。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">deposit</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$codes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>cardId</code> string - 要导入code的卡券ID</li>
<li><code>codes</code> array - 要导入微信卡券后台的自定义 code，最多100个</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>
<span class="token variable">$codes</span>    <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'11111'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'22222'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'33333'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">deposit</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$codes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="查询导入code数目" tabindex="-1"><a class="header-anchor" href="#查询导入code数目" aria-hidden="true">#</a> 查询导入code数目</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">getDepositedCount</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  <span class="token comment">// 要导入 code 的卡券 ID</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">getDepositedCount</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="核查-code-接口" tabindex="-1"><a class="header-anchor" href="#核查-code-接口" aria-hidden="true">#</a> 核查 code 接口</h3>
<p>为了避免出现导入差错，强烈建议开发者在查询完 code 数目的时候核查 code 接口校验 code 导入微信后台的情况。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">check</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$codes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$codes</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'807732265476'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'22222'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'33333'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">check</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$codes</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="查询-code-接口" tabindex="-1"><a class="header-anchor" href="#查询-code-接口" aria-hidden="true">#</a> 查询 Code 接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$checkConsume</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li>checkConsume  是否校验code核销状态，true和false</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'736052543512'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uDgnm0pKfrTb1yV0dFMO_Gk'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token constant boolean">false</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// check_consume = false</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="核销code接口" tabindex="-1"><a class="header-anchor" href="#核销code接口" aria-hidden="true">#</a> 核销Code接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者指定 cardId</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'789248558333'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uDmhkLj6l5bm3cq9iteQBck'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="code-解码接口" tabindex="-1"><a class="header-anchor" href="#code-解码接口" aria-hidden="true">#</a> Code 解码接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">decrypt</span><span class="token punctuation">(</span><span class="token variable">$encryptedCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$encryptedCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'XXIzTtMqCxwOaawoE91+VJdsFmv7b8g0VZIZkqf4GWA60Fzpc8ksZ/5ZZ0DVkXdE'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">decrypt</span><span class="token punctuation">(</span><span class="token variable">$encryptedCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="更改-code-接口" tabindex="-1"><a class="header-anchor" href="#更改-code-接口" aria-hidden="true">#</a> 更改 Code 接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$newCode</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>newCode</code> string - 变更后的有效 Code 码</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'148246271394'</span><span class="token punctuation">;</span>
<span class="token variable">$newCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'659266965266'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$newCode</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="设置卡券失效" tabindex="-1"><a class="header-anchor" href="#设置卡券失效" aria-hidden="true">#</a> 设置卡券失效</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">disable</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span>    <span class="token operator">=</span> <span class="token string single-quoted-string">'736052543512'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">code</span><span class="token operator">-></span><span class="token function">disable</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="通用卡券" tabindex="-1"><a class="header-anchor" href="#通用卡券" aria-hidden="true">#</a> 通用卡券</h2>
<h2 id="卡券激活" tabindex="-1"><a class="header-anchor" href="#卡券激活" aria-hidden="true">#</a> 卡券激活</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">general_card</span><span class="token operator">-></span><span class="token function">activate</span><span class="token punctuation">(</span><span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="撤销激活" tabindex="-1"><a class="header-anchor" href="#撤销激活" aria-hidden="true">#</a> 撤销激活</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">general_card</span><span class="token operator">-></span><span class="token function">deactivate</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="更新用户信息" tabindex="-1"><a class="header-anchor" href="#更新用户信息" aria-hidden="true">#</a> 更新用户信息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">general_card</span><span class="token operator">-></span><span class="token function">updateUser</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="会员卡" tabindex="-1"><a class="header-anchor" href="#会员卡" aria-hidden="true">#</a> 会员卡</h2>
<h3 id="会员卡激活" tabindex="-1"><a class="header-anchor" href="#会员卡激活" aria-hidden="true">#</a> 会员卡激活</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">member_card</span><span class="token operator">-></span><span class="token function">activate</span><span class="token punctuation">(</span><span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<ul>
<li><code>info</code> - 需要激活的会员卡信息</li>
</ul>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$info</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'membership_number'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'357898858'</span><span class="token punctuation">,</span> <span class="token comment">//会员卡编号，由开发者填入，作为序列号显示在用户的卡包里。可与Code码保持等值。</span>
      <span class="token string single-quoted-string">'code'</span>                     <span class="token operator">=></span> <span class="token string single-quoted-string">'916679873278'</span><span class="token punctuation">,</span> <span class="token comment">//创建会员卡时获取的初始code。</span>
      <span class="token string single-quoted-string">'activate_begin_time'</span>      <span class="token operator">=></span> <span class="token string single-quoted-string">'1397577600'</span><span class="token punctuation">,</span> <span class="token comment">//激活后的有效起始时间。若不填写默认以创建时的 data_info 为准。Unix时间戳格式</span>
      <span class="token string single-quoted-string">'activate_end_time'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'1422724261'</span><span class="token punctuation">,</span> <span class="token comment">//激活后的有效截至时间。若不填写默认以创建时的 data_info 为准。Unix时间戳格式。</span>
      <span class="token string single-quoted-string">'init_bonus'</span>               <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//初始积分，不填为0。</span>
      <span class="token string single-quoted-string">'init_balance'</span>             <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//初始余额，不填为0。</span>
      <span class="token string single-quoted-string">'init_custom_field_value1'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'白银'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field1定义类型的初始值，限制为4个汉字，12字节。</span>
      <span class="token string single-quoted-string">'init_custom_field_value2'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'9折'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field2定义类型的初始值，限制为4个汉字，12字节。</span>
      <span class="token string single-quoted-string">'init_custom_field_value3'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'200'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field3定义类型的初始值，限制为4个汉字，12字节。</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token property">member_card</span><span class="token operator">-></span><span class="token function">activate</span><span class="token punctuation">(</span><span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="设置开卡字段" tabindex="-1"><a class="header-anchor" href="#设置开卡字段" aria-hidden="true">#</a> 设置开卡字段</h3>
