<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code>wx.addCard({
    cardList: <span class="token prolog">&lt;?= $json ?></span>, // 需要打开的卡券列表
    success: function (res) {
        var cardList = res.cardList; // 添加的卡券列表信息
    }
